import { baseUrl } from '../common/config.js'
// wx.login  code 
export function accessTemporaryCode(data) {
	return new Promise(function(reslove, reject) {
		uni.request({
			url: `${baseUrl}/user/code`,
			method: 'POST',
			data: data,
			success: res => {
				let result = res;
				console.log(' wx.login  code ', result.data);
				reslove(result.data);
			},
			fail: () => {},
			complete: () => {}
		})
	})
}
