export function makeOrder(data) {
	return new Promise(function(reslove, reject) {
		var token = uni.getStorageSync('token');
		uni.request({
			url: `${baseUrl}/order/makeOrder`,
			method: 'POST',
			data: data,
			header: {
				"authorization": token
			},
			success: res => {
				let result = res;
				console.log('', result.data);
				reslove(result.data);
			},
			fail: () => {},
			complete: () => {}
		})
	})
}

